body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    @include font-smoothing;
    min-width: 320px;
    font-family: $faktPro;
    font-size: 20px;
    line-height: 30px;
    color: #000;
    letter-spacing: -0.2px;
    background: #f0f; }

.ds-parent {
    margin: 0;
    padding: 0;
    overflow: hidden; }

.ds-container {
    position: absolute;
    width: 100%;
    height: 100% !important;
    overflow: hidden;
    z-index: 100;
    transform: none !important; }

// .ds-container > *
//     position: relative
//     width: 100%
//     height: 100%
//     box-sizing: border-box
//     overflow: hidden
//  // transition: 0.3s opacity

.slide-in {
    animation: fade-out 4.5s; }

.slide-out {
    animation: fade-in 1.5s; }

@keyframes fade-out {
    from {opacity: 0};
    to {opacity: 1}; }

@keyframes fade-in {
    from {opacity: 1};
    to {opacity: 0}; }

img {
	max-width: 100%;
	height: auto; }

a {
	text-decoration: none;
	color: inherit; }

ul {
	margin: 0;
	padding: 0 {
    list-style-type: none; } }

button {
    background-color: transparent;
    font-size: 0; }
