.container-inner {
    max-width: 1430px;
    padding: 0 36px;
    margin: 0 auto;
    position: relative; }

.row {
    display: flex;
    flex-wrap: wrap;
    // width: 100%
    margin-right: -30px; }

// .row>[class*="col"]:first-child
//     padding-left: 0

// .row>[class*="col"]:last-child
//     padding-right: 0

.col1 {
    @include col(1,12); }
.col2 {
    @include col(2,12); }
.col3 {
    @include col(3,12); }
.col4 {
    @include col(4,12); }
.col5 {
    @include col(5,12); }
.col6 {
    @include col(6,12); }
.col7 {
    @include col(7,12); }
.col8 {
    @include col(8,12); }
.col9 {
    @include col(9,12); }
.col10 {
    @include col(10,12); }
.col11 {
    @include col(11,12); }
.col12 {
    @include col(12,12); }

[class*="col"] {
    padding-right: 30px; }

.col-24 {
    flex: 0 0 24%; }

.col-56 {
    flex: 0 0 55%; }

.col-31 {
    flex: 0 0 31%; }

.col-45 {
    flex: 0 0 45%;
    padding-right: 50px; }

.col-50 {
    flex: 0 0 50%;
    &:last-child {
        padding-right: 100px; } }
