.top {
    display: inline-block;
    vertical-align: top; }

.bottom {
    display: inline-block;
    vertical-align: bottom; }

.content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100% !important;
    opacity: 0;
    visibility: hidden;
    z-index: 1;
    transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
    overflow: auto;
    @include vertical;
    padding-top: 10vh;
    padding-bottom: 10vh; }


.content.active {
    opacity: 1;
    visibility: visible;
    z-index: 1; }

.wrapper {
    font-size: 20px;
    width: 100%;
    position: relative;
    vertical-align: middle;
    display: inline-block;
    text-align: left;
    height: 100%;
    min-height: 550px; }


