html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "FaktPro";
  font-style: normal;
  font-weight: 400;
  src: local("FaktPro Normal"), local("FaktPro-Normal"), url("../fonts/FaktPro-Normal.woff2") format("woff2"), url("../fonts/FaktPro-Normal.woff") format("woff");
}

.icon-arrow {
  width: 0.93em;
  height: 1em;
  fill: currentColor;
}

.icon-logo-grey {
  width: 3.53em;
  height: 1em;
  fill: currentColor;
}

.icon-logo {
  width: 3.53em;
  height: 1em;
  fill: currentColor;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 320px;
  font-family: "FaktPro";
  font-size: 20px;
  line-height: 30px;
  color: #000;
  letter-spacing: -0.2px;
  background: #f0f;
}

.ds-parent {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.ds-container {
  position: absolute;
  width: 100%;
  height: 100% !important;
  overflow: hidden;
  z-index: 100;
  transform: none !important;
}

.slide-in {
  animation: fade-out 4.5s;
}

.slide-out {
  animation: fade-in 1.5s;
}

@keyframes fade-out {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  padding-list-style-type: none;
}

button {
  background-color: transparent;
  font-size: 0;
}

.container-inner {
  max-width: 1430px;
  padding: 0 36px;
  margin: 0 auto;
  position: relative;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
}

.col1 {
