}

.col4 {
  flex: 0 0 33.33333%;
}

.col5 {
  flex: 0 0 41.66667%;
}

.col6 {
  flex: 0 0 50%;
}

.col7 {
  flex: 0 0 58.33333%;
}

.col8 {
  flex: 0 0 66.66667%;
}

.col9 {
  flex: 0 0 75%;
}

.col10 {
  flex: 0 0 83.33333%;
}

.col11 {
  flex: 0 0 91.66667%;
}

.col12 {
  flex: 0 0 100%;
}

[class*="col"] {
  padding-right: 30px;
}

.col-24 {
  flex: 0 0 24%;
}

.col-56 {
