.header {
  position: absolute;
  top: 36px;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.nav-item:first-child {
  margin-right: 38px;
}

.nav-item--wider {
  margin-right: 54px !important;
}

.nav-item {
  display: inline-block;
}

.nav-link.is-active {
  color: #ff4d00;
}

.nav-arrow {
  visibility: hidden;
  opacity: 0;
  transition: 0.8s opacity, 0.8s visibility;
}

.nav-item:hover .nav-arrow {
  visibility: visible;
  opacity: 1;
}

.logo {
  display: block;
  max-width: 88px;
  position: relative;
  z-index: 10000;
}

.toggler-container {
  position: relative;
}

.footer {
  position: absolute;
  z-index: 200;
  bottom: 42px;
  left: 0;
  width: 100%;
  transform: translate3d(0, 0, 0);
}

.is-hidden {
  display: none;
}

.is-visible {
  display: block;
}

.pagination {
  position: absolute;
  bottom: 0;
  right: 42px;
  z-index: 300;
  font-size: 18px;
  color: #abadb0;
}

.top {
  display: inline-block;
  vertical-align: top;
}

.bottom {
  display: inline-block;
  vertical-align: bottom;
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100% !important;
  opacity: 0;
  visibility: hidden;
  z-index: 1;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
  overflow: auto;
  text-align: center;
  font-size: 0;
  padding-top: 10vh;
  padding-bottom: 10vh;
}

.content:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.content.active {
  opacity: 1;
  visibility: visible;
  z-index: 1;
}

.wrapper {
  font-size: 20px;
  width: 100%;
  position: relative;
  vertical-align: middle;
  display: inline-block;
  text-align: left;
  height: 100%;
  min-height: 550px;
}

.titleLarge {
  font-size: 20px;
  line-height: 30px;
  font-weight: 400;
}

.title {
  font-size: 18px;
