}

.link {
  color: #abadb0;
}

.margin-140 {
  margin-bottom: 140px;
}

.margin-32 {
  margin-bottom: 32px;
}

.margin-80 {
  margin-bottom: 65px;
  align-items: center;
}

.max-width-290 {
  max-width: 275px;
}

.margin-56 {
  margin-bottom: 56px;
}

.center-text-p-90 {
  max-width: 336px;
  padding: 90px 0;
}

.margin-112 {
  margin-bottom: 112px;
}

.center-text-p47 {
