}

.margin-42 {
  margin-bottom: 42px;
}

.start-right {
  max-width: 277px;
}

.feature-list {
  line-height: 30px;
}

.label {
  color: #abadb0;
  font-size: 18px;
}

.label-small .label {
  font-size: 17px;
}

.flex-column {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.menu {
  position: fixed;
  display: none;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  text-align: center;
  font-size: 0;
}

.menu:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.menu__top {
  margin-top: 38px;
  margin-bottom: 6vh;
  position: relative;
}

.menu__container {
  height: 100%;
}

.menu__item {
  font-size: 50px;
  line-height: 50px;
  margin-bottom: 30px;
}

.menu__item.is-active a {
  color: #ff4d00;
  border-bottom: 2px solid #ff4d00;
}

.menu__motto {
  max-width: 320px;
  font-size: 17px;
  line-height: 23px;
  color: #abadb0;
}

.menu__main-bottom {
  margin-bottom: 110px;
}

.menu__bottom {
  position: absolute;
  max-width: 1430px;
  width: calc(100% - 36px);
  margin: 0 auto;
  bottom: 60px;
}

.margin-110 {
  margin-bottom: 10vh;
}

.margin-64 {
  margin-bottom: 64px;
}

.social__item {
  display: inline-block;
  margin-right: 60px;
}

.social__arrow {
  visibility: hidden;
  opacity: 0;
  transition: 0.8s opacity, 0.8s visibility;
}

.social__arrow:hover .social__arrow {
  visibility: visible;
  opacity: 1;
}

.flex-between {
  display: flex;
  justify-content: space-between;
}

.menu-trigger {
  position: absolute;
  top: 0;
  right: 24px;
  cursor: pointer;
  z-index: 10000;
  padding: 20px;
}

.menu-trigger span {
  display: inline-block;
  height: 4px;
  width: 4px;
  border-radius: 50%;
  background-color: #000;
  transition: 0.8s transform;
  position: relative;
  z-index: 10000;
  color: #fff;
}

.menu-trigger span:nth-child(2) {
  margin: 0 6px;
}

.menu-trigger.menu-is-open span {
  background-color: #fff;
}

.menu-trigger.menu-is-open span:nth-child(2),
.menu-trigger.menu-is-open span:nth-child(3) {
  display: none;
}

.menu-trigger:hover span:nth-child(1) {
  animation: bounce 0.7s;
}

.menu-trigger:hover span:nth-child(2) {
  animation: bounce 0.7s;
  animation-delay: 0.2s;
}

.menu-trigger:hover span:nth-child(3) {
  animation: bounce 0.7s;
  animation-delay: 0.2s;
}

@keyframes bounce {
  from,
  20%,
  53%,
  80%,
  to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0);
  }
  40%,
  43% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -2px, 0);
  }
  70% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    animation-timing-function-transform: translate3d(0, 0, 0);
  }
  90% {
    transform: translate3d(0, 2px, 0);
  }
}
